@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;600&display=swap);
._bc_cbb {
  color: #3B3B3B;
  font-size: 13px;
  line-height: 14px;
}

._bc_cbb * {
  box-sizing: border-box;
  font-family: 'Open Sans', sans-serif;
}

._bc_cbb ._bc_cbb_btn {
  background: none;
  outline: none;
  border: none;
  padding: 0;
  margin: 0;
  cursor: pointer;
}

/* Button Default */
._bc_cbb ._bc_cbb_btn--default {
  width: 48px;
  height: 48px;
  display: block;
  border-radius: 50%;
  position: relative;
  text-decoration: none;
  margin: 0 auto;
}

._bc_cbb ._bc_cbb_btn--small {
  width: 36px;
  height: 36px;
}

._bc_cbb ._bc_cbb_btn--default ._bc_cbb_btn__icon {
  width: 100%;
  -webkit-transform: scale(1);
          transform: scale(1);
  transition: all .3s ease;
}

._bc_cbb ._bc_cbb_btn--default ._bc_cbb_btn__title {
  font-weight: 600;
  font-size: 12px;
  line-height: 13px;
  color: #53BB01;
  position: absolute;
  right: calc(100%);
  white-space: nowrap;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  opacity: 0;
  transition: all .3s ease;
}

._bc_cbb ._bc_cbb_btn--default:hover ._bc_cbb_btn__icon {
  -webkit-transform: scale(0.95);
          transform: scale(0.95);
}

._bc_cbb ._bc_cbb_btn--default:hover ._bc_cbb_btn__title {
  opacity: 1;
  visibility: visible;
  right: calc(100% + 12px);
}

._bc_cbb ._bc_cbb_btn--view_more {
  position: absolute;
  background: none;
  bottom: 100%;
  right: 12px;
  color: #FFFFFF;
}

._bc_cbb ._bc_cbb_btn--view_more svg {
  position: absolute;
  z-index: 1;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

._bc_cbb ._bc_cbb_btn--view_more::before {
  content: '';
  width: 100%;
  height: 100%;
  border-radius: 50%;
  top: 0;
  left: 0;
  position: absolute;
  display: block;
  background-color: rgba(0, 0, 0, 0.15);
}

._bc_cbb ._bc_cbb_btn--view_more:hover::before {
  background-color: var(--cbb-color);
  opacity: 0.3;
}

._bc_cbb ._bc_cbb_btn--view_more:hover {
  color: var(--cbb-color);
}

/* Button Chat */
._bc_cbb ._bc_cbb_btn--chat {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #FFFFFF;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.15);
  position: relative;
  transition: all .3s ease;
}

._bc_cbb ._bc_cbb_btn--chat.opened {
  -webkit-transform: scale(0.8);
          transform: scale(0.8);
}

._bc_cbb ._bc_cbb_btn--chat.opened ._bc_cbb_btn__cta {
  display: none;
}

._bc_cbb ._bc_cbb_btn--chat.opened ._bc_cbb_btn__icon--chat {
  display: none;
}

._bc_cbb ._bc_cbb_btn--chat.opened ._bc_cbb_btn__icon--close {
  display: block;
}

._bc_cbb ._bc_cbb_btn--chat ._bc_cbb_btn__icon {
  position: relative;
  z-index: 1;
  width: 26px;
  height: auto;
}

._bc_cbb ._bc_cbb_btn--chat ._bc_cbb_btn__icon--close {
  display: none;
}

._bc_cbb ._bc_cbb_btn--chat ._bc_cbb_btn__underlay {
  display: block;
  position: absolute;
  z-index: 0;
  width: 60px;
  height: 60px;
  top: 50%;
  left: 50%;
  -webkit-transform: translate3d(-50%, -50%, 0);
          transform: translate3d(-50%, -50%, 0);
  -webkit-animation: cbb-button-underlay 3s infinite;
          animation: cbb-button-underlay 3s infinite;
  border-radius: 50%;
}

._bc_cbb ._bc_cbb_btn--chat ._bc_cbb_btn__cta {
  font-size: 12px;
  background: rgba(255, 255, 255, 0.3);
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.15);
  padding: 0 17px;
  height: 25px;
  border-radius: 13px;
  white-space: nowrap;
  display: inline-flex;
  align-items: center;
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  right: calc(100% + 10px);
  cursor: pointer;
  color: var(--cbb-color);
  transition: .15s color, .15s background-color;
}

._bc_cbb ._bc_cbb_btn--chat ._bc_cbb_btn__cta:hover {
  color: #FFFFFF;
  background-color: var(--cbb-color);
}

/* Greeting */
._bc_cbb ._bc_cbb_greeting {
  width: 240px;
  font-size: 12px;
  line-height: 13px;
}

._bc_cbb ._bc_cbb_greeting__heading {
  background: #FFFFFF;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
  padding: 15px 17px;
  position: relative;
}

._bc_cbb ._bc_cbb_greeting__close {
  width: 25px;
  height: 25px;
  position: absolute;
  right: 0;
  bottom: 100%;
  background: rgba(0, 0, 0, 0.2);
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
  border-radius: 50%;
  color: #FFFFFF;
  transition: background-color 0.15s;
}

._bc_cbb ._bc_cbb_greeting__close:hover {
  background: rgba(0, 0, 0, 0.4);
}

._bc_cbb ._bc_cbb_greeting__close svg {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

._bc_cbb ._bc_cbb_greeting__account {
  display: flex;
  align-items: center;
  margin-bottom: 3px;
}

._bc_cbb ._bc_cbb_greeting__avatar {
  width: 24px;
  height: 24px;
  border-radius: 50%;
  border: 2px solid #E6E7E8;
  display: flex;
  align-items: center;
  justify-content: center;
}

._bc_cbb ._bc_cbb_greeting__avatar img {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  object-fit: cover;
}

._bc_cbb ._bc_cbb_greeting__name {
  color: rgba(59, 59, 59, 0.7);
  font-weight: 600;
  margin-left: 5px;
}

._bc_cbb ._bc_cbb_greeting__location {
  color: #9B9A9B;
  font-weight: 300;
  margin-left: 10px;
}

._bc_cbb ._bc_cbb_greeting__welcome {
  color: #3B3B3B;
}

._bc_cbb ._bc_cbb_greeting__links {
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
}

._bc_cbb ._bc_cbb_greeting__links li {
  list-style: none;
  margin-top: 10px;
}

._bc_cbb ._bc_cbb_greeting__links li + li {
  margin-left: 8px;
}

._bc_cbb ._bc_cbb_greeting__links a {
  display: inline-flex;
  align-items: center;
  background: #FFFFFF;
  height: 32px;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.15);
  border-radius: 16px;
  padding: 0 10px;
  text-decoration: none;
  font-weight: 600;
  color: var(--cbb-color);
  transition: color .3s ease, background-color .3s ease;
}

._bc_cbb ._bc_cbb_greeting__links a:hover {
  background-color: var(--cbb-color);
  color: #FFFFFF;
}

/* Form */
._bc_cbb_form_heading {
  font-size: 16px;
  line-height: 17px;
  color: #FFFFFF;
  background-color: #F79719;
  padding: 15px 25px;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}

._bc_cbb_form_heading--no-bg {
  background-color: transparent;
  color: #F79719;
  padding: 0;
}

._bc_cbb_form_group {
  margin-bottom: 15px;
}

._bc_cbb_form_group._bc_cbb_mb-0 {
  margin-bottom: 0;
}

._bc_cbb_form_group ._bc_cbb_form_label {
  font-size: 13px;
  line-height: 14px;
  color: #3B3B3B;
  margin-bottom: 7px;
  display: inline-block;
}

._bc_cbb_form_group ._bc_cbb_form_control {
  width: 100%;
  height: 36px;
  padding: 0 15px;
  outline: none;
  background: #FFFFFF;
  border: 1px solid #F1F1F1;
  box-sizing: border-box;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.05);
  border-radius: 5px;
}

._bc_cbb_form_group ._bc_cbb_form_control.invalid {
  box-shadow: 0 0 2px 1px #ac1f2d;
}

._bc_cbb_form_group ._bc_cbb_form_control--select {
  -webkit-appearance: none;
  -moz-appearance: none;
  text-indent: 1px;
  text-overflow: '';
  background-image: url("data:image/svg+xml,%0A%3Csvg width='10' height='6' viewBox='0 0 10 6' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M0.209209 0.494918C0.488155 0.215971 0.940416 0.215971 1.21936 0.494918L5 4.27556L8.78064 0.494917C9.05958 0.215971 9.51184 0.215971 9.79079 0.494917C10.0697 0.773863 10.0697 1.22612 9.79079 1.50507L5.50508 5.79079C5.22613 6.06974 4.77387 6.06974 4.49492 5.79079L0.209209 1.50507C-0.0697367 1.22613 -0.0697367 0.773864 0.209209 0.494918Z' fill='%23291D1E'/%3E%3C/svg%3E%0A");
  background-repeat: no-repeat;
  background-position: calc(100% - 15px) center;
}

._bc_cbb_form_group ._bc_cbb_form_control--select::-ms-expand {
  display: none;
}

._bc_cbb_form_group--large ._bc_cbb_form_control {
  /* height: 50px; */
  border: 1px solid #F1F1F1;
  box-shadow: 0px 4px 4px rgb(0 0 0 / 5%);
  border-radius: 5px;
  padding: 0 15px;
  font-size: 14px;
  line-height: 18px;
}

._bc_cbb_form_inner {
  padding: 25px 27px;
}

._bc_cbb_form_submit {
  width: 100%;
  height: 35px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
  outline: none;
  padding: 0;
  border: 1px solid #F1F1F1;
  box-sizing: border-box;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.1);
  border-radius: 5px;
  background: #F79719;
  color: #FFFFFF;
  cursor: pointer;
}

._bc_cbb_form_submit--large {
  /* height: 50px; */
  border: none;
  box-shadow: 0px 4px 10px rgb(0 0 0 / 15%);
}

._bc_cbb_form_title {
  font-weight: 600;
  display: block;
  font-size: 20px;
  line-height: 25px;
  text-align: center;
  margin-bottom: 30px;
}

._bc_cbb_form_submit--text {
  background-color: transparent;
  box-shadow: none;
  border: none;
  color: #000000;
}

._bc_cbb_form_inline {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
}

._bc_cbb_form_check {
  margin-top: 10px;
  margin-bottom: 25px;
}

._bc_cbb_form_check + ._bc_cbb_form_check {
  margin-left: 94px;
}

._bc_cbb_form_check ._bc_cbb_form_check_input {
  display: none;
}

._bc_cbb_form_check ._bc_cbb_form_check_label {
  font-size: 13px;
  line-height: 14px;
  color: #3B3B3B;
  position: relative;
  padding-left: 30px;
  display: inline-block;
  min-height: 20px;
}

._bc_cbb_form_check ._bc_cbb_form_check_label:before,
._bc_cbb_form_check ._bc_cbb_form_check_label:after {
  content: '';
  border-radius: 50%;
  position: absolute;
  display: block;
}

._bc_cbb_form_check ._bc_cbb_form_check_label:before {
  width: 20px;
  height: 20px;
  border: 1px solid #DEDEDE;
  top: -4px;
  left: 0;
}

._bc_cbb_form_check ._bc_cbb_form_check_label:after {
  width: 10px;
  height: 10px;
  left: 6px;
  top: 2px;
  background-color: #F79719;
  opacity: 0;
}

._bc_cbb_form_check ._bc_cbb_form_check_input:checked ~ ._bc_cbb_form_check_label::before {
  border-color: #F79719;
}

._bc_cbb_form_check ._bc_cbb_form_check_input:checked ~ ._bc_cbb_form_check_label::after {
  opacity: 1;
}

._bc_cbb_form--basic {
  width: 410px;
  background: #FFFFFF;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.15);
  border-radius: 5px;
  overflow: hidden;
}

._bc_cbb_form--advance {
  width: 700px;
  background: #FFFFFF;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.15);
  overflow: hidden;
  border-radius: 10px;
}

._bc_cbb_form--advance ._bc_cbb_form_inner {
  max-width: 550px;
  margin: 0 auto;
}

._bc_cbb_form--advance ._bc_cbb_form_inline {
  justify-content: space-between;
  align-items: center;
}

._bc_cbb_form--advance ._bc_cbb_form_inline  + ._bc_cbb_form_inline {
  margin-top: 15px;
}

._bc_cbb_form--advance ._bc_cbb_form_inline ._bc_cbb_form_group {
  margin: 0;
}

._bc_cbb_form--advance ._bc_cbb_form_group--phone {
  position: relative;
}

._bc_cbb_form--advance ._bc_cbb_form_group--phone ._bc_cbb_ico_phone {
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  left: 10px;
  width: 22px;
  height: auto;
}

._bc_cbb_form--advance ._bc_cbb_form_group--phone ._bc_cbb_form_control {
  padding-left: 42px;
}

._bc_cbb_form--advance ._bc_cbb_form_col_left {
  width: calc(100% - 165px);
}

._bc_cbb_form--advance ._bc_cbb_form_col_right {
  width: 150px;
}

._bc_cbb_form--advance ._bc_cbb_form_note {
  margin-top: 25px;
  text-align: center;
}

._bc_cbb_form--advance ._bc_cbb_form_row {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

._bc_cbb_form--advance ._bc_cbb_form_col {
  width: calc(50% - 7.5px);
}

._bc_cbb_form--advance ._bc_cbb_form_control--textarea {
  height: 137px;
  padding: 12px 15px;
  resize: none;
}

._bc_cbb_tab {
  display: flex;
  margin: 0 0 20px 0;
  padding: 0;
}

._bc_cbb_tab__item {
  width: 33.33%;
  list-style: none;
}

._bc_cbb_tab__item.active button {
  color: var(--cbb-color);
}

._bc_cbb_tab__item button {
  width: 100%;
  height: 67px;
  padding: 0 10px;
  margin: 0;
  outline: none;
  display: flex;
  border: none;
  background: none;
  align-items: center;
  justify-content: center;
  font-size: 12px;
  line-height: 13px;
  position: relative;
  flex-direction: column;
}

._bc_cbb_tab__item button svg {
  width: 20px;
  height: auto;
  margin-bottom: 7px;
}

._bc_cbb_tab__item + ._bc_cbb_tab__item button::before {
  content: "";
  display: block;
  width: 1px;
  height: 46px;
  background-color: #e0e0e0;
  position: absolute;
  left: 0;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}

._bc_cbb_form_step {
  display: none;
}

._bc_cbb_form_step.active {
  display: block;
}

._bc_cbb_copyright {
  color:#FFFFFF;
  font-size: 12px;
  padding-top: 10px;
  text-align: center;
}

._bc_cbb_copyright a {
  color: #FFFFFF;
  text-decoration: none;
  font-weight: 700;
}

/* Callback */
._bc_cbb_modal {
  position: fixed;
  z-index: 1080;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  visibility: hidden;
  opacity: 0;
  transition: all .35s ease-in-out;
}

._bc_cbb_modal.show {
  visibility: visible;
  opacity: 1;
}

._bc_cbb_modal.hide {
  visibility: hidden;
  opacity: 0;
}

._bc_cbb_modal ._bc_cbb_btn--close {
  width: 25px;
  height: 25px;
  border-radius: 50%;
  background-color: #FFFFFF;
  color: #000000;
  z-index: 5;
  position: absolute;
  top: 10px;
  right: 10px;
}

._bc_cbb_modal ._bc_cbb_btn--close svg {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

._bc_cbb_modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4);
  z-index: 1;
}

._bc_cbb_modal__content {
  position: relative;
  z-index: 5;
}

/* Chat Bubble */
._bc_cbb {
  position: fixed;
  z-index: 1070;
  right: 24px;
  bottom: 20px;
}

._bc_cbb__inner {
  position: absolute;
  width: 60px;
  bottom: 100%;

}

._bc_cbb__inner ._bc_cbb_btn {
  margin-bottom: 10px;
}

._bc_cbb__group--2 {
  position: absolute;
  width: 100%;
  bottom: 100%;
  right: 0;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s, visibility 0.3s, -webkit-transform 0.3s;
  transition: transform 0.3s, opacity 0.3s, visibility 0.3s;
  transition: transform 0.3s, opacity 0.3s, visibility 0.3s, -webkit-transform 0.3s;
  z-index: 999;
  -webkit-transform: scale(.7);
          transform: scale(.7);
}

._bc_cbb__group--2.active {
  opacity: 1;
  visibility: visible;
  -webkit-transform: scale(1);
          transform: scale(1);
}

._bc_cbb__group--greeting {
  position: absolute;
  right: 0;
  bottom: calc(100% + 10px);
  z-index: 5;
  visibility: hidden;
  opacity: 0;
  transition: all .35s ease-in-out;
}

._bc_cbb__group--greeting.show {
  opacity: 1;
  visibility: visible;
}

._bc_cbb__group--greeting.hiden {
  opacity: 0;
  visibility: hidden;
}

/* Animate */
@-webkit-keyframes cbb-button-underlay {
  0% {
    opacity: .25;
    width: 96%;
    height: 96%;
  }

  40% {
    opacity: .25;
  }

  100% {
    opacity: 0;
    width: 150%;
    height: 150%;
  }
}

@keyframes cbb-button-underlay {
  0% {
    opacity: .25;
    width: 96%;
    height: 96%;
  }

  40% {
    opacity: .25;
  }

  100% {
    opacity: 0;
    width: 150%;
    height: 150%;
  }
}

@media screen and (max-width: 767px) {
  ._bc_cbb_form--basic {
    width: calc(100% - 30px);
    margin: 0 auto;
  }
  
  ._bc_cbb_form--advance {
    width: calc(100% - 30px);
    margin: 0 auto;
  }
  
  ._bc_cbb_form_check + ._bc_cbb_form_check {
    margin-left: 15px;
  }
}
